          }
        }
      },

      drag(el) {
        let oDiv = el;  // 当前元素
        let firstTime = "", lastTime = "";  //声明时间用来区分拖拽和单机
        document.onselectstart = function () {       // 禁止选择网页上的文字
          return false;
        }
        oDiv.onmousedown = function (e) {
          let disX = e.clientX - oDiv.offsetLeft          // 鼠标按下，计算当前元素距离可视区的距离    // 算出鼠标相对元素的位置     // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
          let disY = e.clientY - oDiv.offsetTop
          document.getElementById("dragbtn").setAttribute("data-flag", false);
          document.getElementById("dragbtn").setAttribute("show2", oDiv.offsetLeft);
          firstTime = new Date().getTime();
          document.onmousemove = function (e) {
            let l = e.clientX - disX               // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置 左侧多少 ，不是定型尺寸哦 ，是定位尺寸
            let t = e.clientY - disY
            // console.log(e.clientX+"移动后的鼠标位置 ，已经和这个没移动前不一样了  ")
            if (l <= 0) {
              l = 0;
            } else if (l >= document.documentElement.clientWidth - oDiv.clientWidth) {
              //document.documentElement.clientWidth 屏幕的可视宽度
              l = document.documentElement.clientWidth - oDiv.clientWidth
            }
            document.getElementById("dragbtn").setAttribute("show2", l.toString()); //无法使用this，只好赋值给属性
            if (t <= 0) {
              t = 0;
            } else if (t >= document.documentElement.clientHeight - oDiv.clientHeight) {  //本身的宽高  oDiv.clientHeight
              t = document.documentElement.clientHeight - oDiv.clientHeight              // document.documentElement.clientHeight 屏幕的可视高度
            }
            oDiv.style.left = l + 'px'              // 移动当前元素
            oDiv.style.top = t + 'px'
          }
          document.onmouseup = function (e) {
            document.onmousemove = null;
            document.onmouseup = null;
            lastTime = new Date().getTime();
            if ((lastTime - firstTime) < 200) {
              document.getElementById("dragbtn").setAttribute("data-flag", true)
            }
          }
          return false          // return false不加的话可能导致黏连，就是拖到一个地方时div粘在鼠标上不下来，相当于onmouseup失效
        }
      }
    },
    methods: {

      //保留小数点后两位
      inputChange(value, key) {
        value[key] = value[key].replace(/[^\d.]/g, "");  //清除“数字”和“.”以外的字符
        value[key] = value[key].replace(/\.{2,}/g, "."); //只保留第一个. 清除多余的
        value[key] = value[key].replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
        value[key] = value[key].replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3');//只能输入两个小数
        if (value[key].indexOf(".") < 0 && value[key] != "") {//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
          value[key] = parseFloat(value[key]);
          value[key] = value[key] + ''
          if (value[key].length > 2) {
            value[key] = value[key].slice(0, 2)
          }
        }
