                console.log("异步调用出错:" + JSON.stringify(error));
                loading.close();
                // this.companyDialogFormVisible =false;//保存失败关闭dialog
                this.$alert(JSON.stringify(error.message), '新增出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:" + JSON.stringify(res));
                loading.close();
                // this.companyDialogFormVisible =false;//保存失败关闭dialog
                this.$alert(JSON.stringify(res.message), '新增出错', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              });
            } else {
              let _jsondata_ = {
                di_uuid: that.companyForm.di_uuid,
                di_online_status: that.companyForm.di_online_status,
                di_address_type: that.companyForm.di_address_type,
                di_setup_position: that.companyForm.di_setup_position,
                di_long: that.companyForm.di_long,
                di_lat: that.companyForm.di_lat,
                di_code: that.companyForm.di_code,
                di_index_page: that.companyForm.di_index_page,
              };
              const loading = this.$loading({
                lock: true,
                text: '提交中...',
                target: '.main',
                spinner: 'el-icon-loading',

                background: 'rgba(0, 0, 0, 0)'
              });
              //编辑
              var p5 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.deviceInfoBank_update,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                { "Content-Type": "application/x-www-form-urlencoded" },
                this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
              );
              p5.then((res) => {
                console.log("异步调用成功返回:" + JSON.stringify(res));
                if (res.data.status == 'success') {
                  this.companyDialogFormVisible = false;//保存成功关闭dialog
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration: 3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">设备信息修改成功 </span>'
                  });
                  this.reQueryValveCurrentData();
                } else {
                  console.info("修改失败");
                  loading.close();
                  // this.companyDialogFormVisible =false;//保存失败关闭dialog
                  // this.$alert('登录已超时，请重新登录！', '', {
                  //   confirmButtonText: '确定',
                  //   callback: action => {
                  //     console.info('data:' + JSON.stringify(res.data));
                  //     if (res.data.msg === '请登录认证') {
                  //       this.$router.push({path: '/login'});
                  //     }
                  //   }
                  // });
                  if (res.data.msg === '请登录认证') {
                    this.$router.push({ path: '/login' });
                  }
                }

              }, error => {
                console.log("异步调用出错:" + JSON.stringify(error));
                loading.close();
                // this.companyDialogFormVisible =false;//保存失败关闭dialog
                this.$alert(JSON.stringify(error.message), '修改出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:" + JSON.stringify(res));
                loading.close();
                // this.companyDialogFormVisible =false;//保存失败关闭dialog
                this.$alert(JSON.stringify(res.message), '修改出错', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              });
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      //平台服务费设置
      platform_service_fee(index, row) {
        this.platform_service_fee_dialog = true;
        this.tableDialgTitle = '平台服务费设置';
        this.platform_service_feeForm.bsi_name = row.bsi_name;
        this.platform_service_feeForm.di_uuid = row.di_uuid
        if (row.di_address_type === '1') {
          this.platform_service_feeForm.di_address_type = '设备内';
        } else if (row.di_address_type === '2') {
          this.platform_service_feeForm.di_address_type = '设备外';
        }
        this.platform_service_feeForm.di_setup_position = row.di_setup_position;
        this.platform_service_feeForm.di_station_id = row.di_station_id;
        this.platform_service_fee_select();
      },
      /**
        * 平台保存货道图 /fr/postPloatFormSaveGraphForDevice
        * @param //deviceId 设备ID
        * @param //deviceActiveId 设备ID
        * @param //orgId 网点ID
        * @param //registerKey 注册码
        * @param //graph 货道图
      */
      postPloatFormSaveGraphForDeviceClick(index, row) {
        console.log(index, row);
        this.postPloatFormSaveGraphForDeviceDialog = true
        this.postPloatFormSaveGraphForDeviceMsg.index = index
        this.postPloatFormSaveGraphForDeviceMsg.row = row

      },
      //平台服务费设置弹窗关闭
      platform_service_fee_cancel() {
        this.platform_service_fee_dialog = false;
      },
      //平台服务费设置弹窗保存
      platform_service_fee_submit() {
        let that = this;
        if (that.platform_service_feeForm.mbpb_fee === '') {
          that.platform_service_feeForm.mbpb_fee = '0';
        }
        let _jsondata_ = {
          mbpb_bank_station_uuid: that.platform_service_feeForm.di_station_id,
          mbpb_device_uuid: that.platform_service_feeForm.di_uuid,
          mbpb_fee: that.platform_service_feeForm.mbpb_fee,
        };
        var d2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Platform_equipment_rate_add,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {
            that.platform_service_fee_dialog = false;

          } else {
            console.info("平台服务费设置失败");
            // loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          this.$alert(JSON.stringify(error.message), '平台服务费设置出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          this.$alert(JSON.stringify(res.message), '平台服务费设置出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

      //转13位id
      bank_commodity() {
        let that = this;
        let _jsondata_ = {
          companyId: that.platform_service_feeForm.di_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var s1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.bank_transcoding,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        s1.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            that.transcoding_id = res.data.currentCompanyId;
            that.postBank13CompanyIdUnionClick()
          } else {
            console.info("转码失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          this.$alert(error.message, '转码出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(res.message, '转码出错', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },

      postBank13CompanyIdUnionClick() {
        let that = this;
        let _jsondata_ = {
          method: 1,
          bank_company_alias_id: that.transcoding_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var s1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postBank13CompanyIdUnionConfig,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        s1.then((res) => {
          if (res.data.status == 'success') {
            if (res.data.msg) {
              this.mid = JSON.parse(res.data.msg).mid
              loading.close();
              that.platform_service_fee_submit1()
              that.platform_service_fee_submit()
            } else {
              loading.close();
              this.$alert('请先配置银联支付', '配置出错', {
                confirmButtonText: '确定',
                callback: action => {

                }
              });
            }

          } else {
            console.info("转码失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          this.$alert(JSON.stringify(error.message), '转码出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '转码出错', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },


      //平台服务费设置弹窗保存
      platform_service_fee_submit1() {
        let that = this;
        if (that.platform_service_feeForm.mbpb_fee === '') {
          that.platform_service_feeForm.mbpb_fee = '0';
        }
        let _jsondata_ = {
          flag: 1,
          obj_id: this.mid,
          bank_company_alias_id: that.transcoding_id,
          device_uuid: that.platform_service_feeForm.di_uuid,
          obj_fee: that.platform_service_feeForm.mbpb_fee,
        };
        var d2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postBank13CompanyIdAndDeviceUnionConfigExt,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {
            that.platform_service_fee_dialog = false;
            that.platform_service_feeForm.mbpb_fee = '0';
          } else {
            console.info("平台服务费设置失败");
            // loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          // loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '平台服务费设置出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          // loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '平台服务费设置出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

      //平台服务费设置弹窗查询
      platform_service_fee_select() {
        let that = this;
        let _jsondata_ = {
          obj_station_id: that.platform_service_feeForm.di_station_id,
          mbpb_device_uuid: that.platform_service_feeForm.di_uuid,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var d2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Equipment_platform_service_fee_select,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            if (res.data.rows.length !== 0) {
              that.platform_service_feeForm.mbpb_fee = res.data.rows[0].mbpb_fee;
              console.log('平台服务费设置:' + JSON.stringify(that.platform_service_feeForm.mbpb_fee));
            }
          } else {
            console.info("平台服务费设置查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '平台服务费设置查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '平台服务费设置查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
    }
  }
</script>

